import React, { useState, useEffect } from 'react';
import NftToken from '../build/NFT';
import NFT1155 from '../build/NFT1155';
import web3 from '../build/web3';
const Moralis = require('moralis');
Moralis.initialize('9jTSEy7eKGdH8EBCOvH5zCYM01TxksUQcItcbfQg');
Moralis.serverURL = 'https://dzfl3tiplyqt.usemoralis.com:2053/server';
let currentUser = Moralis.User.current();

function BlockchainFunctions() {
  // Set Variables
  const [userAddress, setUserAddress] = useState(null);
  const [balances, setBalances] = useState(null);
  const [balancesNFT, setBalancesNFT] = useState(null);
  const [isUploading, setIsUploading] = useState(false);
  const [fileUploaded, setFileUploaded] = useState(null);
  const [fileName, setFileName] = useState('');
  const [fileURI, setFileURI] = useState(null);
  const [mediaType, setMediaType] = useState(null);
  let currentUser = Moralis.User.current();

  // Login with Moralis Function
  const login = async () => {
    try {
      Moralis.Web3.authenticate().then(function (user) {
        setUserAddress(user.get('ethAddress'));
      });
      console.log("moralis connected")
    } catch (error) {
      console.log(error);
    }
  };

  // Logout from Moralis
  const logout = async () => {
    try {
      Moralis.User.logOut().then(() => {
        currentUser = Moralis.User.current();
        setUserAddress(null);
      });
    } catch (error) {
      console.log(error);
    }
  };

  // Get BEP-20 balances of userAddress
  const getBalances = async () => {
    if (userAddress) {
      try {
        const options = { chain: 'bsc testnet', address: userAddress };
        const tokens = await Moralis.Web3.getAllERC20(options);
        setBalances(tokens);
        console.log(balances);
      } catch (error) {
        console.log(error);
      }
    }
  };

  // Get NFT Balances of userAddress
  const getNFTBalance = async () => {
    try {
      const options = {
        chain: 'bsc testnet',
        address: window.ethereum.selectedAddress,
      };
      const BscNFT = await Moralis.Web3.getNFTs(options);
      setBalancesNFT(BscNFT);
      console.log(BscNFT);
    } catch (error) {
      console.log(error);
    }
  };

  //Upload File to IPFS
  async function uploadFile(e) {
    setIsUploading(true);
    try {
      const data = e.target.files[0];
      const fileToUpload = new Moralis.File(data.name, data);
      console.log(data, fileToUpload);
      await fileToUpload.saveIPFS();
      setFileUploaded(fileToUpload._ipfs);
      setFileURI(fileToUpload._hash);
      if (data.type.includes('video') || data.type.includes('audio')) {
        setMediaType('video');
      } else if (data.type.includes('image')) {
        setMediaType('image');
      }
    } catch (error) {
      console.log(error);
    }
    setIsUploading(false);
  }

  // Send transaction to contract
  const createNFT = async (file, NFTDetails) => {
    try {
      let instance = await NftToken();
      
      const newNFT = await instance.methods
        .create(file._hash, NFTDetails)
        .send({ from: window.ethereum.selectedAddress });

        return newNFT
    } catch (error) {
      console.log(error);
    }
  };

  

  return {
    createNFT,
    createMultiNFT,
    login,
  };
}

export const login = async () => {
  try {
    Moralis.Web3.authenticate().then(function (user) {
      console.log(user.get('ethAddress'));
    });

    return currentUser.get('ethAddress');
  } catch (error) {
    console.log(error);
  }
};

export const logout = async () => {
  try {
    Moralis.User.logOut().then(() => {
      console.log(Moralis.User.current());
    });
    return currentUser.get('ethAddress');
  } catch (error) {
    console.log(error);
  }
};

export async function uploadFile(data) {
  try {
    const fileToUpload = new Moralis.File(data.name, data);
    fileToUpload.type = data.type;
    await fileToUpload.saveIPFS();
    

    return fileToUpload;
  } catch (error) {
    console.log(error);
  }
}

export const createNFT = async (URI, metaData) => {
  try {
    let instance = await NftToken();
    let data = JSON.stringify(metaData);
    console.log(data);
    const newNFT = await instance.methods
      .create(URI, data)
      .send({ from: window.ethereum.selectedAddress });

    console.log(newNFT);
    return newNFT;
  } catch (error) {
    console.log(error);
  }
};

export const createMultiNFT = async (URI, metaData) => {
  try {
    let instance = await NFT1155();
    let data = JSON.stringify(metaData);
    console.log(data);
    const newNFT = await instance.methods
      .create(metaData.amount, URI, data)
      .send({ from: window.ethereum.selectedAddress });

    console.log(newNFT);
    return newNFT;
  } catch (error) {
    console.log(error);
  }
};

export const getNFTBalance = async () => {
    try {
      const options = {
        chain: 'bsc testnet',
        address: window.ethereum.selectedAddress,
      };
      const BscNFT = await Moralis.Web3.getNFTs(options);      
      console.log(BscNFT);
      return BscNFT
    } catch (error) {
      console.log(error);
    }
  };

export const extraInfoNFT = async (id, typeNFT) => {

  if(typeNFT === "ERC721") {
    try {
       let instance = await NftToken();
      let extraInfo = await instance.methods.extraInfoMap(id).call()
      return JSON.parse(extraInfo.metaData)
    } catch (error) {
      console.log(error)
    }
  } else if(typeNFT === "ERC1155") {
    try {
      let instance = await NFT1155();
      let extraInfo = await instance.methods.extraInfoMap(id).call()      
      return JSON.parse(extraInfo.metaData)
    } catch (error) {
      console.log(error)
    }
  }
  

}