import React, {useState} from 'react'
import MultiNFT from "./components/createMultiNFT"
import SingleNFT from "./components/createSingleNFT"
import ToggleButton from "@material-ui/lab/ToggleButton";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

function Create() {
    const [createSingleNFT, setCreateSingleNFT] = useState (true)

    return (
        <Paper className="form_content" style={{
              
              display: 'grid',
              'place-items': 'center',
            }}>
            <ToggleButtonGroup>
            <ToggleButton selected={createSingleNFT} value="left" aria-label="left aligned" className="bb" onClick={() => setCreateSingleNFT(true)}>
              Sigle NFT
            </ToggleButton>
            <ToggleButton selected={!createSingleNFT} aria-label="justified" className="bbb" onClick={() => setCreateSingleNFT(false)}>
              Multiple NFT
            </ToggleButton>
          </ToggleButtonGroup>
            
          {createSingleNFT ? <SingleNFT /> : <MultiNFT />}

        </Paper>
    )
}

export default Create
