import React, {useEffect, useState} from 'react'
import { extraInfoNFT } from 'blockchain/blockchain-functions/functions';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';



function NFTCard({props}) {
    const {amount, contract_type, token_address, token_id, token_uri} = props
    const [NFTinfo, setNFTinfo] = useState ({
        description:"",
        location:"",
        name:"",
        type:""
    })

     const useStyles = makeStyles({
  root: {
    maxWidth: 300,
  },
  });

const classes = useStyles();

const getExtraInfo = async () => {
  let result = await extraInfoNFT(token_id, contract_type)

  try {
    const {description, location, name, type} = result
    setNFTinfo({
        description, location, name, type
    })

  } catch (error) {
      console.log(error)
  }  
 
}

useEffect(() => {
    getExtraInfo()
}, [])
     

  return (
    <Card key={token_id} className={classes.root}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt="Contemplative Reptile"
          height="140"
          image={contract_type === "ERC1155" ? `https://ipfs.moralis.io:2053/ipfs/${token_uri}` : token_uri}
          title="Contemplative Reptile"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="h2">
           
             {NFTinfo.location}
          </Typography>
            <Typography color="textSecondary" variant="h5" component="h5">
            {NFTinfo.name}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {NFTinfo.description}
          </Typography>
          
        </CardContent>
      </CardActionArea>
      <CardActions>
        <Button size="small" color="primary">
          Share
        </Button>
        <Button size="small" color="primary">
          Learn More
        </Button>
      </CardActions>
    </Card>
  );
            }

export default NFTCard;
